<template lang="pug">
    AuthLayout(:title="title" :more="more" :disable="form.disable")
        template(#form)
            v-form(ref="form" lazy-validation)
                p // TODO
        template(#action)
            v-btn(
                block
                color="blue darken-1"
                :loading="form.loading"
                :disabled="form.disable"
                @click="submit"
            ).mx-2.mb-2.white--text {{ forget }}
        template(#app)
