</template>

<script>
    import AuthLayout from '@/components/auth/AuthLayout.vue'
    import Snackbar from '@/components/common/Snackbar.vue'
    export default {
        name: "AuthForget",
        components: {
            AuthLayout, Snackbar
        },
        data () {
            return {
                title: '找回密码',
                forget: '找回',
                more: {
                    text: '更多选项',
                    items: [
                        { title: '登录账户?', to: '/login' },
                        { title: '注册帐号?', to: '/register' }
                    ],
                },
                form: {
                    email: null,
                    disable: false,
                    loading: false
                }
            }
        },
        methods: {
            async submit (evt) {
                evt.preventDefault();
            }
        }
    }
</script>

